/**
 * 消息状态描述。
 */
export const MessageState = {

    /**
     * 
     */
    Unknown: 0,

    /**
     * 未发送。
     */
    Unsent: 1,

    /**
     * 发送中。
     */
    Sending: 1 << 1,

    /**
     * 已发送。
     */
    Sent: 1 << 2,

    /**
     * 已读。
     */
    Read: 1 << 3,

    /**
     * 已召回。
     */
    Recalled: 1 << 4,

    /**
     * 消息出现故障。
     */
    Fault: 1 << 5
}
